<template>
  <div class="side-menu-wrapper">
    <slot></slot>
    <Menu width="auto" theme="dark" v-show="!collapsed" :active-name="$route.name" :open-names="openItem" @on-select="handleSelect" @on-open-change="toggerMenu">
      <template v-for="item in list">
        <re-submenu
          v-if="item.children"
          :key="`menu_${item.name}`"
          :name="item.name"
          :parent="item"
        >
          <menu-item></menu-item>
        </re-submenu>
        <menu-item v-else :key="`menu_${item.name}`" :name="item.name">
          <Icon :type="item.icon"/>
          {{ item.title }}
        </menu-item>
      </template>
    </Menu>
    <div class="drop-wrapper" v-show="collapsed">
      <template v-for="item in list">
        <re-dropdown @on-select="handleSelect" v-if="item.children" icon-color="#fff" :show-title="false" :key="`drop_${item.name}`" :parent="item"></re-dropdown>
        <Tooltip v-else transfer :content="item.title" placement="right" :key="`drop_${item.name}`">
          <span @click="handleClick(item.name)" class="drop-menu-span">
            <Icon :type="item.icon" color="#fff" :size="30"/>
          </span>
        </Tooltip>
      </template>
    </div>
  </div>
</template>

<script>
import ReSubmenu from './re-submenu'
import ReDropdown from './re-dropdown'
export default {
  name: 'index',
  components: {
    're-submenu': ReSubmenu,
    're-dropdown': ReDropdown
  },
  props: {
    collapsed: {
      type: Boolean,
      default: false
    },
    list: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      openItem: []
    }
  },
  created () {
    this.openItem = JSON.parse(localStorage.getItem('openItem'))
  },
  methods: {
    handleSelect (name) {
      // console.log(name)
      this.$router.push({
        name: name,
        query: {}
      })
    },
    handleClick (name) {
      // console.log(name)
      this.$router.push({
        name: name,
        query: {}
      })
    },
    toggerMenu: function (name) {
      this.openItem = name
      // console.log(name.toString())
      localStorage.setItem('openItem', JSON.stringify(name))
    },
    openActiveState: function () {
      this.openItem = []
      if (this.$route.matched.length > 2) {
        this.openItem.push(this.$route.matched[0].name)
        this.openItem.push(this.$route.matched[1].name)
      } else {
        this.openItem.push(this.$route.matched[0].name)
      }
    }
  },
  mounted () {
  }
}
</script>

<style scoped>
.side-menu-wrapper{}
.side-menu-wrapper .ivu-tooltip{}
.side-menu-wrapper .ivu-tooltip,.drop-menu-span{
  display: block;
  width: 100%;
  text-align: center;
  padding: 10px 0;
}
.side-menu-wrapper .drop-wrapper > .ivu-dropdown {
  display: block;
  padding: 10px;
  margin: 0 auto;
}
</style>



