<template>
  <Menu width="auto"
        :active-name="activeName"
        :open-names="openNames"
        @on-select="handleSelect"
  >
    <template v-for="item in items">
      <side-menu-item
        v-if="item.children&&item.children.length!==0"
        :parent-item="item"
        :key="'menu-'+item.name"
      >
      </side-menu-item>
      <menu-item v-else
                 :name="item.name"
                 :key="'menu-'+item.name"
      >
        <Icon :type="item.icon" :size="15"/>
        <span>{{ item.title }}</span>
      </menu-item>
    </template>
  </Menu>
</template>

<script>
import sideMenuItem from './side_menu_item'
export default {
  name: 'left_menu_nav',
  components: {
    'side-menu-item': sideMenuItem
  },
  props: {
    activeName: {
      type: String,
      default: 'auth'
    },
    openNames: {
      type: Array,
      default: () => [
        'other',
        'role',
        'auth'
      ]
    },
    items: {
      type: Array,
      default: () => ({})
    }
  },
  methods: {
    handleSelect (name) {
      this.$emit('on-select', name)
    }
  }
}
</script>

<style scoped>
</style>



