<template>
  <div class="layout" ref="lay" :style="{width: pageWidth+'px',height: pageHeight+'px'}">
    <Layout>
      <Sider ref="side" class="layout-sider" hide-trigger collapsible :collapsed-width="70" v-model="isCollapsed">
        <!--<left-menu-nav :items="routes"></left-menu-nav>-->
        <side-menu :collapsed="isCollapsed" :list="routes"></side-menu>
      </Sider>
      <Layout>
        <Header class="layout-header-bar">
          <Icon @click.native="collapsedSider" :class="rotateIcon" class="layout-header-bar-icon" type="md-menu" size="24"></Icon>
        </Header>
        <Content class="layout-content">
          <router-view ref="contentView"/>
        </Content>
      </Layout>
    </Layout>
  </div>
</template>

<script>
import routers from '@/router/api/index'
import leftMenuNav from '@/components/common/page/left_menu/left_menu_nav.vue'
import sideMenu from '@/components/common/page/side_menu/index.vue'
export default {
  name: 'test',
  metaInfo: {
    title: '测试页demo',
    meta: [
      {charset: 'utf-8'}
    ]
  },
  data () {
    return {
      isCollapsed: false,
      routes: routers,
      pageWidth: this.getViewStyle().width,
      pageHeight: this.getViewStyle().height,
      layout: {
        headerWidth: 0,
        headerHeight: 0,
        contentWidth: 0,
        contentHeight: 0,
        sideWidth: 0,
        sideHeight: 0
      }
    }
  },
  computed: {
    rotateIcon () {
      return [
        'menu-icon',
        this.isCollapsed ? 'rotate-icon' : ''
      ]
    },
    menuitemClasses () {
      return [
        'menu-item',
        this.isCollapsed ? 'collapsed-menu' : ''
      ]
    }
  },
  created () {
  },
  methods: {
    collapsedSider: function () {
      this.$refs.side.toggleCollapse()
      this.countLayoutStyle()
      this.$refs.contentView.$emit('layoutValue', this.layout)
    },
    countLayoutStyle: function () {
      this.$set(this.layout, 'headerWidth', this.$refs.lay.getElementsByClassName('layout-header-bar')[0].offsetWidth)
      this.$set(this.layout, 'headerHeight', this.$refs.lay.getElementsByClassName('layout-header-bar')[0].offsetHeight)
      this.$set(this.layout, 'contentWidth', this.$refs.lay.getElementsByClassName('layout-content')[0].offsetWidth)
      this.$set(this.layout, 'contentHeight', this.$refs.lay.getElementsByClassName('layout-content')[0].offsetHeight)
      this.$set(this.layout, 'sideWidth', this.$refs.lay.getElementsByClassName('layout-sider')[0].offsetWidth)
      this.$set(this.layout, 'sideHeight', this.$refs.lay.getElementsByClassName('layout-sider')[0].offsetHeight)
      // Vuex 异步保存这些参数
      this.$store.dispatch('setPageStyle', this.layout)
      // Vuex 同步保存这个参数
      // this.$store.commit('setPageStyle', this.layout)
    }
  },
  mounted () {
    let _self = this
    this.countLayoutStyle()
    this.$refs.contentView.$emit('layoutValue', this.layout)
    //
    window.onresize = function () {
      _self.countLayoutStyle()
      _self.$refs.contentView.$emit('layoutValue', _self.layout)
      _self.pageWidth = _self.getViewStyle().width
      _self.pageHeight = _self.getViewStyle().height
    }
    console.log(this.getViewStyle())
  },
  components: {
    'left-menu-nav': leftMenuNav,
    'side-menu': sideMenu
  }
}
</script>

<style scoped>
.layout{
  border: 1px solid #d7dde4;
  background:#f5f7f9;
  position:relative;
  /*border-radius: 4px;*/
  overflow: hidden;
}
.layout-sider{
  /*background-color: #ffffff;*/
}
.layout-header-bar{
  background-color: #ffffff;
  box-shadow: 0px 1px 1px rgba(0,0,0,.1) ;
}
.layout-header-bar-icon{
  margin: 0;
}
.layout-content{
  background-color: #ffffff;
}
.layout-logo-left{
  width:90%;
  height:30px;
  background: #5b6270;
  border-radius: 3px;
  margin:15px auto;
}
.rotate-icon{
  transform: rotate(-90deg);
}
</style>



